.page-footer {
  background-color: #ebe7de;
  border-top: 1px solid $mine-shaft;
  margin-top: 150px;
  @include md {
    margin-top: 90px;
  }
  @include sm {
    margin-top: 50px;
  }
  &__btn-burger {
    display: none;
    @include xxl {
      margin-top: 120px;
    }
    @include lg {
      display: inline-block;
      position: absolute;
      right: 0;
      top: -104px;
    }
    @include md {
      margin-top: 150px;
      top: -132px;
    }
    @include sm {
      margin-top: 120px;
      top: -114px;
    }
  }
  &__col {
    @include lg {
      width: 100%;
    }
  }
  &__top {
    .container {
      padding: 68px 40px;
      @include sm {
        padding: 30px 14px;
      }
    }
  }
  &__row {
    position: relative;
    &:first-child {
      display: flex;
      justify-content: space-between;
    }
    &:last-child {
      position: relative;
      min-height: 120px;
      width: 100%;
      .subscribe {
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        margin: 0;
        @include xxl {
          left: 0;
        }
        form {
          width: 460px;
          @include sm {
            width: 100%;
          }
        }
      }
      @include sm {
        margin-top: 20px;
      }
    }
  }
  &__block-group {
    display: flex;
    flex-wrap: wrap;
    max-width: 640px;
    margin-right: 30px;
    @include lg {
      display: none;
    }
  }
  &__item {
    margin-bottom: 90px;
    &:not(:nth-child(3n + 1)) {
      margin-left: 90px;
    }
    &-title {
      font-weight: 700;
      font-size: 18px;
      line-height: 21px;
      color: $mine-shaft;
    }
    &-list {
      list-style: none;
      margin: 0;
      padding: 0;
      &-item {
        &:not(:last-child) {
          margin-bottom: 10px;
        }
      }
      &-link {
        font-weight: 400;
        font-size: 14px;
        line-height: 16px;
        text-transform: uppercase;
        color: #959595;
        text-decoration: none;
        transition: color 0.3s;
        &:hover {
          color: #2d2d2d;
        }
        &:active {
          opacity: 0.5;
        }
      }
    }
  }
  .contacts {
    margin-top: 36px;
    &__link {
      p,
      span {
        border: none;
        @include sm {
          font-size: 9px;
          line-height: 12px;
        }
      }
    }
    &__email {
      display: inline-block;
      margin-top: 10px;
    }
  }
  .social {
    margin-top: 26px;
    &__item {
      position: relative;
      padding: 24px;
      &:not(:last-child) {
        margin-right: 8px;
      }
      @include sm {
        padding: 12px;
      }
    }
    &__link {
      display: inline-block;
      position: absolute;
      border: 1px solid $mine-shaft;
      border-radius: 100%;
      box-sizing: border-box;
      width: 100%;
      height: 100%;
    }
    &__icon {
      left: 50%;
      top: 50%;
      transform: translate(-50%, -45%);
      position: absolute;
      fill: $mine-shaft;
      @include sm {
        &--fb {
          width: 13;
          height: 12px;
        }
        &--insta {
          width: 12px;
          height: 14px;
        }
        &--vk {
          width: 12px;
          height: 14px;
        }
      }
    }
  }

  .logo {
    img {
      @include xxl {
        width: 300px;
        height: 40px;
      }
      @include sm {
        width: 130px;
        height: 18px;
      }
    }
  }
  &__contacts-block {
    @include lg {
      display: flex;
      justify-content: space-between;
    }
  }
  &__bottom {
    .container {
      padding-top: 40px;
      padding-bottom: 40px;
      border-top: 1px solid $mine-shaft;
      display: flex;
      justify-content: space-between;
      @include md {
        display: block;
      }
    }
    &-link {
      text-decoration: none;
      text-transform: uppercase;
      font-weight: 700;
      font-size: 14px;
      line-height: 21px;
      text-align: right;
      color: #2d2d2d;
      opacity: 0.4;
      transition: opacity 0.3s;
      &:hover {
        opacity: 1;
      }
      &:active {
        opacity: 0.6;
      }
      @include md {
        display: inline-block;
        width: 100%;
        text-align: center;
        font-size: 12px;
        line-height: 14px;
        &:not(:last-child) {
          margin-bottom: 20px;
        }
      }
    }
    @include sm {
      margin-top: 20px;
    }
  }
}
