.hero {
  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 50px;
    padding-bottom: 50px;
    @include xl {
      display: block;
      &--narrow {
        padding-left: 150px;
        @include md {
          padding-left: 40px;
        }
        @include sm {
          padding-left: 14px;
        }
      }
    }
    @include sm {
      padding-top: 30px;
      padding-bottom: 30px;
    }
  }
  &__links {
    background-color: #ebe7de;
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    border-top: 1px solid $mine-shaft;
    border-bottom: 1px solid $mine-shaft;
    @include xl {
      display: block;
    }
    &-item {
      width: 50%;
      padding: 30px 110px;
      position: relative;
      &:not(:last-child) {
        border-right: 1px solid $mine-shaft;
        @include xl {
          border-right: none;
          border-bottom: 1px solid $mine-shaft;
        }
      }
      @include xl {
        width: 100%;
        padding: 30px 40px;
      }
      @include sm {
        padding: 30px 14px;
      }
    }
    &-link {
      position: relative;
      display: inline;
      text-decoration: none;
      font-weight: normal;
      font-size: 48px;
      line-height: 56px;
      text-align: right;
      text-transform: uppercase;
      color: #2d2d2d;
      transition: opacity 0.3s;
      &:hover {
        opacity: 0.7;
      }
      &:active {
        opacity: 0.5;
      }
      @include large {
        font-size: 36px;
        line-height: 40px;
      }
      @include xxl {
        font-size: 28px;
        line-height: 32px;
      }
      @include xl {
        display: inline-block;
        text-align: left;
        font-size: 48px;
        line-height: 52px;
      }
      @include md {
        font-size: 24px;
        line-height: 26px;
      }
      @include sm {
        font-size: 18px;
        line-height: 20px;
        max-width: 60%;
      }
      .quantity {
        position: absolute;
        right: auto;
        top: -12px;
      }
    }
    &-icon {
      position: absolute;
      width: 61px;
      height: 35px;
      background-color: #555555;
      right: 40px;
      border-radius: 30px;
      top: 50%;
      transform: translateY(-50%);
      &::before {
        content: "";
        position: absolute;
        left: 50%;
        top: 50%;
        background-image: url(../img/icon-arrow.svg);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        width: 16px;
        height: 16px;
        transform: translate(-50%, -50%) rotate(145deg);
      }
    }
  }
  &__slider {
    position: relative;
    .swiper-slide-active {
      width: 40% !important;
      @include lg {
        width: 50% !important;
      }
      @include md {
        width: 100% !important;
      }
    }
    &-slide {
      width: 33%;
      height: 460px;
      @include lg {
        width: 50%;
      }
      img {
        width: 100%;
        height: 387px;
      }
      p {
        font-weight: 400;
        font-size: 12px;
        line-height: 14px;
        color: #2d2d2d;
        padding-left: 50px;
        max-width: 40%;
        display: block;
      }
    }
    &-navigation {
      position: absolute;
      top: 0;
      z-index: 100;
      width: 100%;
      height: 100%;
    }
    .slider-button {
      &--prev {
        left: 120px;
        @include xl {
          left: 60px;
        }
        @include lg {
          left: 40px;
        }
        @include sm {
          left: 20px;
        }
      }
      &--next {
        right: 120px;
        @include xl {
          right: 60px;
        }
        @include lg {
          right: 40px;
        }
        @include sm {
          right: 20px;
        }
      }
    }
  }
  &__title {
    margin-right: 30px;
    @include sm {
      font-size: 24px;
      line-height: 28px;
    }
  }
  &__footer {
    margin-top: 30px;
  }
}
