.main-nav {
  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;

    @include large {
      display: none;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: 26px;
    }
  }

  &__link {
    color: $mine-shaft;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;
    text-decoration: none;
    transition: opacity 0.3s;
    &:hover {
      opacity: 0.6;
    }
    &:active {
      opacity: 0.5;
    }
  }
  &__btn-burger {
    top: 60px;
    display: none;
    @include large {
      display: inline-block;
      position: absolute;
      right: 40px;
    }
    @include sm {
      display: inline-block;
      position: absolute;
      right: 14px;
      top: 30px;
    }
  }
}
