.language {
  display: flex;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
  &__item {
    &:not(last-child) {
      margin-right: 18px;
    }
  }
  &__btn {
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    color: $mine-shaft;
    transition: opacity 0.3s;
    &:hover {
      opacity: 0.6;
    }
    &:active {
      opacity: 0.5;
    }
  }
}
