.tags-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  max-width: 400px;
  &__item {
    box-sizing: border-box;
    padding: 7px 20px;
    border: 1px solid $mine-shaft;
    border-radius: 32px;
    margin-top: 7px;
    margin-right: 7px;
  }
  &__link {
    text-decoration: none;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;
    color: $mine-shaft;
  }
}
