.blog-card {
  &--large {
    display: flex;
    position: relative;
    padding-top: 70px;
    padding-bottom: 70px;
    border-bottom: 1px solid $mine-shaft;
    .blog-card__title {
      position: absolute;
      font-size: 36px;
      line-height: 48px;
      max-width: 40%;
      right: 50px;
    }
    .blog-card__row {
      width: 50%;
      &:last-child {
        margin-top: 145px;
        padding-left: 120px;
        box-sizing: border-box;
      }
    }
  }
  &__title {
    font-weight: 700;
    font-size: 24px;
    line-height: 32px;
    text-transform: uppercase;
    color: #2d2d2d;
    max-width: 50%;
  }
  &__tags {
    display: flex;
    text-transform: uppercase;
    margin-top: 40px;
    span {
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      color: #959595;
      margin-right: 6px;
    }
    &-list {
      list-style: none;
      margin: 0;
      padding: 0;
      display: flex;
    }
    &-item {
      &:not(:last-child) {
        margin-right: 6px;
        .blog-card__tags-link {
          position: relative;
          &::after {
            content: ",";
          }
        }
      }
    }
    &-link {
      text-decoration: none;
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      color: #959595;
    }
  }
  &__descr {
    margin-top: 40px;
    max-width: 67%;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #2d2d2d;
  }
  &__btn-more {
    margin-top: 40px;
  }
}

