.author {
  display: flex;
  align-items: center;
  margin-top: 30px;
  img {
    display: block;
    border-radius: 100%;
    margin-right: 15px;
  }
  &__text-block {
  }
  &__title {
    margin: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;
    color: #2d2d2d;
  }
  &__position {
    margin: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    color: #959595;
    margin-top: 4px;
  }
}
