.arhievenements {
  display: flex;
  align-items: center;
  &__slider {
    width: 760px;
    @include xxl {
      margin-left: 50%;
      transform: translateX(-50%);
    }
    .swiper-wrapper {
      @include xxl {
        margin-bottom: 40px;
      }
    }
  }
  &__slider-slide {
    display: flex;
    align-items: center;
    padding: 16px 30px;
    box-sizing: border-box;
    border: 1px solid $mine-shaft;
    border-radius: 60px;
    max-width: 250px;
    &:not(:last-child) {
      margin-right: 10px;
    }
    b {
      font-weight: 700;
      font-size: 36px;
      line-height: 48px;
      text-transform: uppercase;
      color: #2d2d2d;
      @include lg {
        font-size: 24px;
        line-height: 32px;
      }
    }
    p {
      font-weight: 400;
      font-size: 14px;
      line-height: 16px;
      text-transform: uppercase;
      color: #959595;
      padding-left: 12px;
    }
  }
  &__slider-scrollbar {
    display: none !important;
    @include md {
      display: block !important;
    }
  }
}
