.social {
  display: flex;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
  &__item {
    &:not(:last-child) {
      margin-right: 20px;
    }
  }
  &__icon {
    fill: #aaa8a5;
    &--fb {
      margin-bottom: 1px;
    }
  }
  &__link {
    transition: opacity 0.3s;
    &:hover {
      opacity: 0.6;
    }
    &:active {
      opacity: 0.5;
    }
  }
}
