.application-section {
  .container {
    padding-top: 130px;
    padding-left: 40px;
    border-top: 1px solid $mine-shaft;
    @include md {
      padding-top: 90px;
    }
    @include sm {
      padding-top: 50px;
    }
  }
  &__inner {
    margin-left: 50%;
    @include xl {
      margin-left: 30%;
    }
    @include lg {
      margin-left: 4%;
    }
    @include sm {
      margin-left: 0;
    }
  }
  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #2d2d2d;
    @include md {
      font-size: 12px;
      line-height: 18px;
    }
  }
  &__title-form {
    font-weight: 700;
    font-size: 18px;
    line-height: 21px;
    margin: 0;
    color: #2d2d2d;
    margin-top: 60px;
    margin-bottom: 12px;
    @include md {
      margin-top: 50px;
    }
    @include sm {
      font-size: 16px;
      line-height: 20px;
    }
  }
}
