.contacts {
  &__link {
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    text-decoration: none;
    color: $mine-shaft;
  }
  &__tel,
  &__address {
    border-bottom: 1px solid $mine-shaft;
    margin: 0;
    transition: opacity 0.3s;
    &:hover {
      opacity: 0.6;
    }
    &:active {
      opacity: 0.5;
    }
  }
  &__email {
    transition: opacity 0.3s;
    &:hover {
      opacity: 0.6;
    }
    &:active {
      opacity: 0.5;
    }
  }
  &__address {
    margin-top: 10px;
  }
}
