.advantages {
  .container {
    margin-top: 150px;
    padding-bottom: 150px;
    @include xxl {
      padding-left: 40px;
      margin-top: 120px;
      padding-bottom: 120px;
    }
    @include md {
      margin-top: 80px;
      padding-bottom: 80px;
    }
    @include sm {
      padding-left: 14px;
      margin-top: 60px;
      padding-bottom: 60px;
    }
  }
  &__slider {
    .swiper-wrapper {
      margin-bottom: 50px;
    }
    .swiper-slide-next {
      background-image: url(../img/advantages.slide-active.jpg);
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
      .advantages__slider-title {
        color: #fdfaf5;
      }
      .advantages__slider-descr {
        color: #fdfaf5;
      }
    }
  }
  .section-title {
    margin-bottom: 70px;
  }
  &__slider-slide {
    border: 1px solid $mine-shaft;
    border-radius: 100%;
    padding: 48px;
    padding-top: 90px;
    box-sizing: border-box;
    width: 355px !important;
    height: 355px !important;
    text-align: center;
    @include xxl {
      padding-top: 120px;
    }
    @include lg {
      padding: 28px;
      padding-top: 62px;
      width: 260px !important;
      height: 260px !important;
    }
  }
  &__slider-title {
    margin: 0;
    font-weight: 400;
    text-align: center;
    font-size: 24px;
    line-height: 28px;
    text-align: center;
    text-transform: uppercase;
    color: #2d2d2d;
    @include large {
      font-size: 22px;
      line-height: 26px;
    }
    @include xxl {
      font-size: 18px;
      line-height: 22px;
    }
  }
  &__slider-descr {
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    text-align: center;
    color: #959595;
    @include xxl {
      font-size: 13px;
      line-height: 15px;
    }
  }
}
