.error-block {
  .container {
    display: flex;
    padding-top: 100px;
    padding-bottom: 70px;
  }
  &__title {
    font-weight: 700;
    font-size: 132px;
    line-height: 116px;
    text-transform: uppercase;
    color: #2d2d2d;
    margin: 0;
  }
  &__inner {
    margin-left: 186px;
    margin-top: 70px;
  }
  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #2d2d2d;
    max-width: 580px;
    margin-bottom: 70px;
  }
}
