.projects {
  padding: 150px 0;
  background-color: #ebe7de;
  @include lg {
    padding-top: 85px;
    padding-bottom: 85px;
  }
  @include sm {
    padding-top: 70px;
    padding-bottom: 70px;
  }
  &__header {
    .container {
      @include md {
        padding-left: 120px;
      }
      @include sm {
        padding-left: 14px;
      }
    }
    &-row {
      width: 60%;
      display: flex;
      justify-content: space-between;
      margin-left: 40%;
      @include lg {
        display: block;
        width: 50%;
        margin-left: 50%;
      }
      @include md {
        display: block;
        width: 40%;
        margin-left: 40%;
      }
      @include sm {
        width: 100%;
        margin-left: 0;
      }
    }
  }
  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
    @include sm {
      display: flex;
      align-items: center;
    }
  }
  &__item {
    @include sm {
      &:not(:last-child) {
        margin-right: 14px;
      }
    }
    &-link {
      text-decoration: none;
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      color: #959595;
      &--active {
        color: $mine-shaft;
      }
      @include md {
        font-size: 14px;
        line-height: 18px;
      }
      @include sm {
        font-size: 12px;
        line-height: 14px;
        white-space: nowrap;
      }
    }
  }
  &__btn-more {
    margin-top: 70px;
    margin-left: 50%;
    transform: translateX(-50%);
  }
  &__slider {
    margin-top: 70px;
    margin-left: 110px;
    max-height: 600px;
    @include xxl {
      margin-left: 0;
      margin-top: 40px;
    }
    &-slide {
      img {
        width: 100%;
        max-height: 557px;
      }
    }
  }
  .btn {
    @include lg {
      margin-top: 30px;
    }
    @include sm {
      padding-top: 18px;
      padding-bottom: 18px;
    }
    @include md {
      margin-top: 70px;
    }
    @include sm {
      margin-top: 30px;
      font-size: 12px;
      line-height: 14px;
    }
  }
  .section-title {
    position: relative;
    display: inline-block;
    @include sm {
      margin-bottom: 24px;
    }
  }
}
