.services {
  .container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .section-title {
    width: 100%;
  }
  &__row {
    &:last-child {
      margin-bottom: 120px;
      @include xl {
        margin-top: 50px;
      }
      @include md {
        margin-top: 110px;
      }
      @include sm {
        margin-top: 80px;
      }
    }
    @include xl {
      width: 100%;
    }
  }
  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  &__item {
    box-sizing: border-box;
    padding: 20px 0;
    border-top: 1px solid #2d2d2d;
    &:last-child {
      border-bottom: 1px solid #2d2d2d;
    }
  }
  &__link {
    font-weight: 400;
    font-size: 24px;
    line-height: 28px;
    text-transform: uppercase;
    color: #2d2d2d;
    text-decoration: none;
    transition: opacity 0.3s;
    &:hover {
      opacity: 0.6;
    }
    &:active {
      opacity: 0.5;
    }
    @include md {
      font-size: 18px;
      line-height: 21px;
    }
    @include sm {
      font-size: 14px;
      line-height: 16px;
    }
  }
  blockquote {
    font-weight: 400;
    font-size: 21px;
    line-height: 24px;
    text-transform: uppercase;
    color: #2d2d2d;
    max-width: 400px;
    margin: 0;
    margin-top: 20px;
    @include sm {
      font-size: 12px;
      line-height: 14px;
    }
  }
  .btn {
    margin-top: 70px;
    @include sm {
      padding-top: 18px;
      padding-bottom: 18px;
      margin-left: 50%;
      transform: translateX(-50%);
    }
  }
}
