.page-header {
  position: relative;
  border-bottom: 1px solid $mine-shaft;
  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    padding: 40px 40px 20px 40px;
    @include sm {
      padding: 20px 14px 20px 14px;
    }
  }
  .social {
    @include large {
      display: none;
    }
  }
  .contacts {
    @include large {
      display: none;
    }
  }
  .language {
    @include large {
      display: none;
    }
  }
  &__btn-submit {
    transition: opacity 0.3s;
    &:hover {
      opacity: 0.9;
    }
    &:active {
      opacity: 0.5;
    }
    @include large {
      margin-right: 70px;
    }
    @include sm {
      display: none;
    }
  }
}
