.about-me {
  .container {
    padding-top: 150px;
    padding-bottom: 150px;
    @include xxl {
      padding-left: 40px;
      padding-top: 120px;
      padding-bottom: 120px;
    }
    @include md {
      padding-top: 70px;
      padding-bottom: 70px;
    }
    @include sm {
      padding-top: 50px;
      padding-bottom: 50px;
    }
  }
  &__text {
    margin-top: 100px;
    margin-left: 50%;
    max-width: 50%;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #2d2d2d;
    @include xxl {
      margin-top: 50px;
    }
    @include md {
      margin-top: 30px;
    }
    @include md {
      margin-left: 58px;
      max-width: 100%;
    }
  }
  &__row {
    margin-top: 112px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include xxl {
      display: block;
      margin-left: 50%;
      margin-top: 50px;
      transform: translateX(-50%);
    }
    @include md {
    }
    .arhievenements {
      justify-content: center;
    }
  }
  &__slider {
    max-height: 720px;
    margin-top: 140px;
    .swiper-wrapper {
      margin-bottom: 50px;
    }
    &-slide {
      img {
        width: 100%;
        max-height: 90%;
      }
    }
    @include xl {
      margin-top: 120px;
    }
    @include md {
      margin-top: 90px;
    }
    @include sm {
      margin-top: 70px;
    }
  }
  .btn {
    @include xxl {
      margin-top: 40px;
      margin-left: 50%;
      transform: translateX(-50%);
      min-width: 260px;
    }
  }
}
