.about-me-detail {
  margin-top: 100px;
  margin-bottom: 150px;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__row {
    width: 50%;
  }
  &__position {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #2d2d2d;
    text-transform: uppercase;
    margin-top: 90px;
    display: inline-block;
  }
  &__title {
    font-weight: 700;
    font-size: 36px;
    line-height: 48px;
    text-transform: uppercase;
    color: #2d2d2d;
    margin: 0;
    max-width: 550px;
    margin-top: 420px;
  }
  &__author {
    display: inline-block;
    width: 100%;
    font-weight: 400;
    margin-top: 30px;
    font-size: 12px;
    line-height: 14px;
    color: #2d2d2d;
  }
  &__textBlock {
    margin-top: 60px;
    display: flex;
    justify-content: space-between;
  }
  &__descr {
    &:not(:last-child) {
      margin-right: 50px;
    }
  }
  video {
    width: 100%;
    height: 762px;
  }
  &__footer {
    margin-top: 70px;
    width: 100%;
  }
}
