.blog {
  .container {
    padding-top: 100px;
    padding-bottom: 150px;
  }
  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #2d2d2d;
    padding-bottom: 40px;
    &-links {
      list-style: none;
      margin: 0;
      padding: 0;
      &-item {
      }
      &-link {
        position: relative;
        text-decoration: none;
        font-weight: 400;
        font-size: 55px;
        line-height: 1;
        color: #2d2d2d;
        opacity: 0.2;
        &:not(:last-child) {
          margin-right: 80px;
          &::before {
            content: "/";
            position: absolute;
            right: -50px;
          }
        }
        &--active {
          opacity: 1;
        }
      }
    }
    &-filters {
      list-style: none;
      margin: 0;
      padding: 0;
      display: flex;
      align-items: center;
      &-item {
        &:not(:last-child) {
          margin-right: 62px;
        }
        &-link {
          text-decoration: none;
          position: relative;
          font-weight: 400;
          font-size: 16px;
          line-height: 24px;
          color: #2d2d2d;
          &::before {
            content: "";
            position: absolute;
            right: -26px;
            top: 50%;
            transform: translateY(-50%) rotate(225deg);
            background-image: url(../img/icon-arrow-black.svg);
            background-repeat: no-repeat;
            background-position: center;
            background-size: contain;
            width: 12px;
            height: 12px;
          }
        }
      }
    }
  }
  &__content {
    &-btn-more {
      margin-left: 50%;
      transform: translateX(-50%);
      margin-top: 70px;
    }
  }
}
